.write {
    padding-top: 50px;
}

.writeImg {
    margin-left: 150px;
    width: 70vw;
    height: 250px;
    border-radius: 15px;
    object-fit: cover;
}

.writeForm {
    position: relative;
}

.writeFormGroup {
    margin-left: 150px;
    display: flex;
    align-items: center;
}

.writeIcon {
    color: #707070;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: 1px solid #3F3F3F;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.writeInput {
    font-size: 30px;
    border: none;
    padding: 20px;
    width: 70vw;
}

.writeInput:focus {
    outline: none;
}

.writeText {
    font-size: 20px;
}

.writeSubmit{
    position: absolute;
    top: 20px;
    right: 250px;
    color: #ceb92c;
    padding: 10px;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    background-color: #3F3F3F;
}